<!-- eslint-disable -->
<!-- 自义定日期列合并表格  -->

<template>
  <div>
    <Table :columns="column1" :data="data" border :span-method="handleSpan"></Table>
  </div>
</template>

<script>
/*eslint no-console:"off" */
/*eslint no-unused-vars: "off"*/
import { watch } from "fs";
export default {
  name: "MyTable",
  props: {
    data: Array,
    date: Boolean, // 是否显示日期
    division: Boolean // 是否显示科室
  },
  data() {
    return {
      map: new Map(),
    };
  },
  methods: {
    // 统计日期在数组中出现的次数
    handleMap() {
      let list = this.data.map(item => {
        return item.date;
      });
      let myMap = new Map();
      list.forEach(item => {
        if (!myMap.has(item)) {
          myMap.set(item, 1);
          return;
        }
        myMap.set(item, myMap.get(item) + 1);
      });
      let num = 0;
      myMap.forEach(key => {
        this.map.set(this.getrow(num, myMap), key);
        num++;
      });
    },
    getrow(num, map) {
      let index = 0;
      let lastIndex = 0;
      try {
        map.forEach(key => {
          if (index === num) {
            throw new Error();
          }
          lastIndex += key;
          index++;
        });
      } catch (e) {
        return lastIndex;
      }
    },
    handleSpan({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (this.map.has(rowIndex)) {
          return {
            rowspan: this.map.get(rowIndex),
            colspan: 1
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
    },
  },
  created() {
    this.handleMap();
  },
  computed: {
    column1() {
      let column = [
        {
          key: "date",
          title: "日期",
          align: "center"
        },
        {
          key: "divisionName",
          title: "科室",
          align: "center"
        },
        {
          key: "drugInduced",
          title: "药物性",
          align: "center",
          children: [
            {
              key: "drugInducedKg",
              title: "重量",
              align: "center"
            },
            {
              key: "drugInducedNum",
              title: "数量",
              align: "center"
            }
          ]
        },
        {
          key: "injury",
          title: "损伤性",
          align: "center",
          children: [
            {
              key: "injuryKg",
              title: "重量",
              align: "center"
            },
            {
              key: "injuryNum",
              title: "数量",
              align: "center"
            }
          ]
        },
        {
          key: "chemical",
          title: "化学性",
          align: "center",
          children: [
            {
              key: "chemicalKg",
              title: "重量",
              align: "center"
            },
            {
              key: "chemicalNum",
              title: "数量",
              align: "center"
            }
          ]
        },
        {
          key: "infectious",
          title: "感染性",
          align: "center",
          children: [
            {
              key: "infectiousKg",
              title: "重量",
              align: "center"
            },
            {
              key: "infectiousNum",
              title: "数量",
              align: "center"
            }
          ]
        },
        {
          key: "pathologic",
          title: "病理性",
          align: "center",
          children: [
            {
              key: "pathologicKg",
              title: "重量",
              align: "center"
            },
            {
              key: "pathologicNum",
              title: "数量",
              align: "center"
            }
          ]
        }
      ];
      if (!this.date) {
        column = column.filter(item => item.key !== 'date');
      }
      if (!this.division) {
        column = column.filter(item => item.key !== 'divisionName');
      }
      return column;
    }
    
  },
  
};
</script>